'use client'
import { useEffect, useState } from "react";
import HeroSection from "./components/HeroSection";
import TabsNavigation from "./components/TabsNavigation";
import PoolRankTable from "./components/PoolRankTable";
import ScoresSearch from "./components/ScoresSearch";
import LiveLeaderboardTable from "./components/LiveLeaderboardTable";
import { calculatePlayerScore } from "./js/golf";
import { DataProvider } from "./context/DataContext";

export function Home() {
  const [activeTab, setActiveTab] = useState("scoreLeaderboard");
  const [tournamentData, setTournamentData] = useState(null);
  const [picksData, setPicksData] = useState(null);
  const [leaderboardData, setLeaderboardData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch("/api/aws/retrieveGolfData");
        const data = await response.json();
        setTournamentData(data.tournamentData);
        const transformedPicksData = Object.values(data.picksData || {}).map((picker) => ({
          ...picker,
          score: calculatePlayerScore(picker, data.tournamentData), // Calculate score for each picker
        }));
        for (const picker of Object.values(data.picksData || {})) {
          picker.score = calculatePlayerScore(picker, data.tournamentData); // Calculate score for each picker
        }
        setPicksData(transformedPicksData);
        setLeaderboardData(data.tournamentData["results"]["leaderboard"]);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, []);

  console.log("Leaderboard data:", tournamentData);
  console.log("Leaderboard data type:", typeof tournamentData);
  console.log("Player data:", picksData);

  return (
    <div>
      <HeroSection />
      <TabsNavigation activeTab={activeTab} setActiveTab={setActiveTab} />
      <div className="tab-content">
        {activeTab === "scoreLeaderboard" && <PoolRankTable players={picksData || []} />}
        {activeTab === "scores" && <ScoresSearch players={picksData || []} />}
        {activeTab === "leaderboard" && <LiveLeaderboardTable leaderboard={leaderboardData || []} />}
      </div>
    </div>
  );
}

export default function App() {
  return (
    <DataProvider>
      <Home />
    </DataProvider>
  );
}
