import React, { createContext, useContext, useEffect, useState } from "react";

const DataContext = createContext(null);

export const DataProvider = ({ children }) => {
  const [data, setData] = useState({ tournamentData: null, picksData: null });

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch("/api/aws/retrieveGolfData");
        const result = await response.json();
        setData({
          tournamentData: result.tournamentData,
          picksData: result.picksData,
        });
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, []);

  return <DataContext.Provider value={data}>{children}</DataContext.Provider>;
};

export const useData = () => useContext(DataContext);

const calculatePlayerScore = (playerName, pickBoard, cutPenalty) => {
    let scoreValue = 0;
    for (let i = 0; i < pickBoard.length; i++) {
        if (playerName === pickBoard[i]["player"]) {
            if (pickBoard[i]["status"] === "cut") {
                scoreValue += cutPenalty * 2;
            }
            scoreValue += pickBoard[i]["score"];
            return scoreValue;
        }
    }
    return scoreValue;
};

